.fileuploadclass {
    font-weight: 400;
    background-color: #547aa0;
    border: none;
    font-size: 16px;
    border-radius: 4px;
    color: white;
    padding: 10px 10px;
    font-family: 'Golos Text', sans-serif;
    box-shadow: 2px 2px 1px 2px rgba(0, 0, 0, .1);
    transition-duration: .4s;
}

.fileuploadclass:hover {
    background-color: #3F5C78;
}